<template>
    <yd-layout class="travelList">
        <div class="handleTabBox">
            <yd-flexbox>
                <yd-flexbox-item>
                    <div class="tabTopL">差旅</div>
                </yd-flexbox-item>
                <yd-flexbox-item>
                    <div @click="closeWebView" class="tabTopR"><img class="imgWork" src="../../../static/images/home@2x.png">工作中心</div>
                </yd-flexbox-item>
            </yd-flexbox>
            <div class="tabBottom">
                <div class="swiper-container">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide active">差旅</div>
                        <div class="swiper-slide" @click="$router.push('/handle')">待办</div>
                        <div class="swiper-slide" @click="$router.push('/performanceList')">绩效</div>
                        <div class="swiper-slide" @click="$router.push('/meetingManage')">会议</div>
                        <div class="swiper-slide" @click="$router.push('/mySchedule/'+getUserInfo.empcode+'/0/my')">日程</div>
                    </div>
                </div>
            </div>
        </div>
        <yd-infinitescroll :callback="loadList" ref="infinitescrollTrave" v-if="status==0">
            <yd-list theme="5" slot="list">
                <yd-list-item v-for="item, key in waitList" :key="key" @click.native="page_jump(item.url)">
                    <yd-list-other slot="other">
                        <span class="list_title">{{item.title}}</span>
                        <span class="list-del-price list_btn">{{item.sysname}}</span>
                    </yd-list-other>
                    <yd-list-other slot="other" class="list_other">
                        <span class="list_info">发起时间：</span>
                        <span class="list_info">{{item.date}}</span>
                    </yd-list-other>
                    <yd-list-other slot="other" class="list_other">
                        <span class="list_info">发起部门：</span>
                        <span class="list_info">办公室</span>
                    </yd-list-other>
                </yd-list-item>
            </yd-list>
            <div slot="doneTip" v-show="page>0">没有数据啦~~</div>
        </yd-infinitescroll>
        <yd-infinitescroll :callback="hasLoadList" ref="infinitescrollHasTrave" v-if="status==1">
            <yd-list theme="5" slot="list">
                <yd-list-item v-for="item, key in hastList" :key="key" @click.native="page_jump(item.url)">
                    <yd-list-other slot="other">
                        <span class="list_title">{{item.title}}</span>
                        <span class="list-del-price list_btn">{{item.sysname}}</span>
                    </yd-list-other>
                    <yd-list-other slot="other" class="list_other">
                        <span class="list_info">发起时间：</span>
                        <span class="list_info">{{item.date}}</span>
                    </yd-list-other>
                    <yd-list-other slot="other" class="list_other">
                        <span class="list_info">发起部门：</span>
                        <span class="list_info">办公室</span>
                    </yd-list-other>
                </yd-list-item>
            </yd-list>
            <div slot="doneTip" v-show="hasPage>0">没有数据啦~~</div>
        </yd-infinitescroll>
        <yd-tabbar class="schedules" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
            <yd-tabbar-item title="待办" v-on:click.native="status=0" :class="{btnActive:status==0}">
                <yd-icon name="waitHandle1" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="已办" v-on:click.native="status=1" :class="{btnActive:status==1}">
                <yd-icon name="hasHandle1" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'travelList',
    data() {
        return {
            waitList: [],
            status: 0,
            page: 0,
            hasPage: 0,
            hastList: []
        }
    },
    mounted() {
        this.loadSwiper();
        this.loadList();
        this.hasLoadList();
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        loadList() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/mobileTravel/getPending', {
                'sysstate': 1,
                'username': vm.getUserInfo.userid,
                'page': vm.page,
                'size': 10,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                if (res.isSuccess == 'true' && res.jsonData != null) {
                    vm.waitList = [...vm.waitList, ...res.jsonData];
                    if (res.jsonData.length < 10) {
                        /* 所有数据加载完毕 */
                        vm.$refs.infinitescrollTrave.$emit('ydui.infinitescroll.loadedDone');
                        return;
                    }
                    vm.$refs.infinitescrollTrave.$emit('ydui.infinitescroll.finishLoad');
                    vm.page++;
                } else if (res.isSuccess == 'true' && res.jsonData == null) {
                    /* 所有数据加载完毕 */
                    vm.$refs.infinitescrollTrave.$emit('ydui.infinitescroll.loadedDone');
                    return;
                }
            })
        },
        hasLoadList() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/mobileTravel/getPending', {
                'sysstate': 2,
                'username': vm.getUserInfo.userid,
                'page': vm.hasPage,
                'size': 10,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                if (res.isSuccess == 'true' && res.jsonData != null) {
                    vm.hastList = [...vm.hastList, ...res.jsonData];
                    if (res.jsonData.length < 10) {
                        /* 所有数据加载完毕 */
                        vm.$refs.infinitescrollHasTrave.$emit('ydui.infinitescroll.loadedDone');
                        return;
                    }
                    vm.$refs.infinitescrollHasTrave.$emit('ydui.infinitescroll.finishLoad');
                    vm.hasPage++;
                } else if (res.isSuccess == 'true' && res.jsonData == null) {
                    /* 所有数据加载完毕 */
                    vm.$refs.infinitescrollHasTrave.$emit('ydui.infinitescroll.loadedDone');
                    return;
                }
            })
        },
        // swiper插件
        loadSwiper() {
            let vm = this;
            vm.$nextTick(() => {
                this.galleryTop = new Swiper('.swiper-container', {
                    initialSlide: 0,
                    slidesPerView: 'auto',
                    paginationClickable: true,
                    observer: true, // 修改swiper自己或子元素时，自动初始化swiper
                    observeParents: true // 修改swiper的父元素时，自动初始化swiper
                })
            });
        },
        page_jump(url) {
            UPCHAT.M.NAPI.startNewWebView(url, { 'sysid': 'upchat-test' });
        }
    }
}
</script>
<style lang="less">
.travelList {
    .yd-list-theme5 {
        background-color: #f8f8f8;
    }

    .yd-list-img {
        display: none;
    }

    .yd-list-item {
        margin-bottom: 0.2rem;
        padding: 0.2rem 0.4rem 0.2rem 0.3rem !important;
        border-radius: 0.18rem;
        background-color: #fff;
    }

    .yd-list-item:after {
        background-image: none;
    }

    .yd-list-mes {
        padding: 0;
    }
    
    .yd-list-other {
        align-items: flex-start;
    }
}
</style>
<style lang="less" scoped>
.yd-list {
    margin: 0.12rem;
    .list_other {
        justify-content: left;
    }
    .list_title {
        width: 5rem;
        word-break: break-all;
        font-size: 0.3rem;
        font-weight: bold;
        line-height: 0.48rem;
        display: inline-block;
        color: #333;
    }

    .list_btn {
        min-width: 1.2rem;
        padding-left: .1rem;
        padding-right: .1rem;
        margin-top: 0.04rem;
        height: 0.44rem;
        text-align: center;
        line-height: 0.44rem;
        background-color: #ff5b2f;
        border-radius: 0.06rem;
        color: #fff;
        font-size: 0.26rem;
        font-weight: bold;
        display: inline-block;
    }

    .list_info {
        font-size: 0.28rem;
        color: #999;
        padding-right: 0.21rem;
        margin-top: 0.26rem;
    }
}

.noBox {
    padding-top: 2.2rem;
    text-align: center;

    img {
        width: 2.5rem;
        margin-bottom: 0.4rem;
    }
}

.swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    width: 1.38rem;
    height: .54rem;
    line-height: .54rem;
    text-align: center;
    font-size: .32rem;
    border-radius: .54rem;
    float: left;
    color: #999;
    margin-right: .2rem;
}

.active {
    color: #fff;
    background: #59A5FD;
    box-shadow: 0 0 0.4rem 0 #d7e9fe;
}

.noWord {
    font-size: 0.28rem;
    color: #666;
    text-align: center;
}

.mainContentFluid {
    position: relative;
    background: #f8f8f8;

    .listWidth {
        padding: 0.12rem;
        background: #f8f8f8;
    }

    .start {
        font-size: 0.28rem;
        color: #999;
        padding-right: 0.21rem;
        margin-top: 0.26rem;
    }

    .startDate {
        font-size: 0.28rem;
        color: #999;
        padding-right: 0.4rem;
        margin-top: 0.26rem;
    }

    .handleList {
        .yd-flexbox[data-v-3ed132a9] {
            align-items: flex-start;
            -webkit-align-items: flex-start;
            position: relative;
            background: #fff;
        }

        .listHandle {
            border-radius: 0.18rem;
            margin-bottom: 0.2rem;
            padding: 0.2rem 0.4rem 0.2rem 0.3rem !important;
            -webkit-align-items: flex-start;
            position: relative;
            background: #fff;
        }

        .handleTitle {
            width: 100%;
            font-size: 0.3rem;
            font-weight: bold;
            line-height: 0.48rem;
            position: relative;
        }

        .tit {
            width: 5rem;
            word-break: break-all;
        }

        .titleBtn {
            position: absolute;
            right: 0;
            top: 0;
            min-width: 1.2rem;
            padding-left: .1rem;
            padding-right: .1rem;
            margin-top: 0.04rem;
            height: 0.44rem;
            text-align: center;
            line-height: 0.44rem;
            background-color: #ff5b2f;
            border-radius: 0.06rem;
            color: #fff;
            font-size: 0.26rem;
            font-weight: bold;
            display: inline-block;
        }
    }

}

.yd-icon-waitHandle:before,
.yd-icon-hasHandle:before {
    display: block;
    content: "";
}

.yd-icon-waitHandle1:before,
.yd-icon-hasHandle1:before {
    display: block;
    content: "";
}

.btnActive .yd-icon-waitHandle:before {
    width: 0.53rem;
    height: 0.55rem;
    background: url(../../../static/images/trs_yl_db.png) 100% 100% no-repeat 0 0;
    background-size: 0.54rem;
}

.btnActive .yd-icon-hasHandle:before {
    width: 0.53rem;
    height: 0.55rem;
    background: url(../../../static/images/trs_yl_05.png) 100% 100% no-repeat 0 0;
    background-size: 0.54rem;
}

.yd-icon-waitHandle1:before {
    width: 0.53rem;
    height: 0.55rem;
    background: url(../../../static/images/trs_yl_09.png) no-repeat center;
    background-size: 0.5rem;
}

.btnActive .yd-icon-waitHandle1:before {
    width: 0.53rem;
    height: 0.55rem;
    background: url(../../../static/images/trs_yl_06.png) no-repeat center;
    background-size: 0.5rem;
}

.yd-icon-hasHandle1:before {
    width: 0.53rem;
    height: 0.65rem;
    background: url(../../../static/images/trs_yl_07.png) no-repeat center;
    background-size: 0.45rem;
}

.btnActive .yd-icon-hasHandle1:before {
    width: 0.53rem;
    height: 0.65rem;
    background: url(../../../static/images/trs_yl_07_avtive.png) no-repeat center;
    background-size: 0.45rem;
}
</style>


